#whole-app {
  height: 100vh;
  width: 100vw;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#Header {
  height: 10vh;
  width: 90vw;
  display: flex;
  align-items: top;
  justify-content: center;
  font-size: 80%;
  padding-bottom: 10vmin;
  padding-right: 5vmin;
}

#Main {
  display: flex;
  justify-content: space-evenly;
  align-items: top;
  text-align: center;
  height: 70vh;
  width: 90vw;
  padding-left: 1vmin;
}

h1, h3, #break-length, #session-length {
  color: seashell;
}

.incDec {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: yellow;
  font-size: 100%;
  font-weight: bold;
  width: 50px;
  height: 50px;
  border-radius: 50px;
}

.flex-third {
  height: 50vh;
  width: 25vw;
  border-radius: 5px;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#timer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#timer-label, #break-label, #session-label {
  height: 5vh;
  font-size: 150%;
}

#time-left {
  text-align: center;
  background-color: darkslateblue;
  color: limegreen;
  border-radius: 20vmin;
  font-size: 300%;
  line-height: 20vh;
  width: 20vw;
  height: 20vh;
}

.timer-buttons {
  display: flex;
  justify-content: space-between;
  align-items: top;
  color: whitesmoke;
  width: 20vw;
  height: 15vh;
  font-size: 100%;
}

#start_stop {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to right,rgb(43, 197, 43),rgb(196, 196, 34));
  height: 5vh;
  width: 8vw;
  border-radius: 5px;
  padding: 1vmin;
}
#reset {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(to right, rgb(196, 196, 34), rgb(240, 4, 51));
  border-radius: 5px;
  height: 5vh;
  width: 8vw;
  padding: 1vmin;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 10vh;
  width: 5vw;
  pointer-events: none;
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg)
  }
}
