body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#whole-app {
  height: 100vh;
  width: 100vw;
  background-color: black;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#Header {
  height: 10vh;
  width: 90vw;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: top;
          align-items: top;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 80%;
  padding-bottom: 10vmin;
  padding-right: 5vmin;
}

#Main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: top;
          align-items: top;
  text-align: center;
  height: 70vh;
  width: 90vw;
  padding-left: 1vmin;
}

h1, h3, #break-length, #session-length {
  color: seashell;
}

.incDec {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-color: yellow;
  font-size: 100%;
  font-weight: bold;
  width: 50px;
  height: 50px;
  border-radius: 50px;
}

.flex-third {
  height: 50vh;
  width: 25vw;
  border-radius: 5px;
  background-color: gray;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

#timer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

#timer-label, #break-label, #session-label {
  height: 5vh;
  font-size: 150%;
}

#time-left {
  text-align: center;
  background-color: darkslateblue;
  color: limegreen;
  border-radius: 20vmin;
  font-size: 300%;
  line-height: 20vh;
  width: 20vw;
  height: 20vh;
}

.timer-buttons {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: top;
          align-items: top;
  color: whitesmoke;
  width: 20vw;
  height: 15vh;
  font-size: 100%;
}

#start_stop {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-image: linear-gradient(to right,rgb(43, 197, 43),rgb(196, 196, 34));
  height: 5vh;
  width: 8vw;
  border-radius: 5px;
  padding: 1vmin;
}
#reset {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background-image: linear-gradient(to right, rgb(196, 196, 34), rgb(240, 4, 51));
  border-radius: 5px;
  height: 5vh;
  width: 8vw;
  padding: 1vmin;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vh;
  width: 5vw;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
}

@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg)
  }
}

